body{
  margin:0;
  background-color: white;
  
  }
  
  .clearfix{
      overflow:auto;
  }
  
  .container{
      display: flex;
      margin:auto;
      max-width: 1280px;
      flex-wrap:wrap;
      background:white;
  }
  
  @media only screen and (min-width:800px){
      .lg-size-25{
          width:24%;
          margin-left:1%;
          margin-bottom:20px;
          margin-top:40px;
      }
  }
  
  @media only screen and (min-width:400px){
      .md-size-50{
          width:48%;
          margin-left:2%;
          margin-bottom:20px;
      }
  }